.list {
    overflow: hidden;
    margin-top: -50px;
    position: relative;
    z-index: 1;
    &__wrapper {
        padding: 90px 70px;
        
        background: rgba(255, 255, 255, 1);

        @media screen and (max-width: 1470px) {
            padding: 90px 40px;
        }

        @include vp-1023 {
           
            padding: 90px 20px;
        }

        @media screen and (max-width: 660px) {
            padding: 90px 20px;
        }

        @include vp-389 {
            padding: 90px 10px 60px;
        }
    }

    &__table {
        display: flex;
        flex-direction: column;
        gap: 26px;

        @media screen and (max-width: 690px) {
            display: none;
        }
    }

    &__top {
        display: grid;
        grid-template-columns: 1fr 1fr 1fr 1fr;
        gap: 20px;
        border-bottom: 1px solid rgba(60, 43, 139, 1);
        padding-bottom: 20px;

        @media screen and (max-width: 1170px) {
            grid-template-columns: 0.5fr 0.5fr 0.5fr 1fr;
        }
    }

    &__top span {
        font-family: Inter;
        font-weight: 700;
        font-style: Bold;
        font-size: 18px;
        leading-trim: NONE;
        line-height: 24.8px;
        letter-spacing: 0%;
        vertical-align: middle;
        color: rgba(60, 43, 139, 1);

        @include vp-1023 {
            font-size: 16px;
        }
    }

    &__top span:nth-child(2),
    &__top span:nth-child(3) {
        justify-self: center;
    }

    &__top span:nth-child(4) {
        justify-self: end;
    }

    &__row {
        display: grid;
        grid-template-columns: 1fr 1fr 1fr 1fr;
        gap: 20px;

        @media screen and (max-width: 1170px) {
            grid-template-columns: 0.5fr 0.5fr 0.5fr 1fr;
        }
    }

    &__row span {
        font-family: Inter;
        font-weight: 400;
        font-style: Regular;
        font-size: 18px;
        leading-trim: NONE;
        line-height: 100%;
        letter-spacing: 0%;
        color: rgba(60, 43, 139, 1);
        min-height: 38px;

        @include vp-1023 {
            font-size: 16px;
        }
    }

    &__row span:nth-child(2),
    &__row span:nth-child(3) {
        justify-self: center;
    }

    &__row span:nth-child(4) {
        justify-self: end;
        text-align: right;
    }

    &__table-mobile {
        display: none;

        @media screen and (max-width: 690px) {
            display: flex;
            flex-direction: column;
            gap: 20px;
        }
    }

    &__item {
        display: flex;
        flex-direction: column;
        gap: 20px;
        border-bottom: 1px solid rgba(60, 43, 139, 1);
        padding-bottom: 20px;
    }

    &__item:last-child {
        border-bottom: none;
        padding-bottom: 0;
    }

    &__content {
        display: flex;
        justify-content: space-between;
        gap: 20px;
    }

    &__content span {
        font-family: Inter;
        font-weight: 700;
        font-style: Bold;
        font-size: 16px;
        leading-trim: NONE;
        line-height: 120%;
        letter-spacing: 0%;
        vertical-align: middle;
        color: rgba(60, 43, 139, 1);

        @include vp-389 {
            font-size: 14px;
        }
    }

    &__content p {
        font-family: Inter;
        font-weight: 400;
        font-style: Regular;
        font-size: 16px;
        leading-trim: NONE;
        line-height: 120%;
        letter-spacing: 0%;
        vertical-align: middle;
        color: rgba(60, 43, 139, 1);
        margin: 0;

        @include vp-389 {
            font-size: 14px;
        }
    }

    &__content:nth-child(4) p {
        text-align: right;
    }
}