.look {
    overflow: hidden;
    margin-top: -50px;
    &__wrapper {
        padding: 90px 70px;
        
        background: rgba(255, 255, 255, 1);
        display: flex;
        flex-direction: column;
        align-items: center;
        @media screen and (max-width: 1470px) {
            padding: 90px 40px;
        }

        @include vp-1023 {
           
            padding: 90px 20px;
        }

        @media screen and (max-width: 660px) {
            padding: 90px 20px;
        }

        @include vp-389 {
            padding: 90px 10px 60px;
        }
    }

    &__title {
        font-family: Coolvetica;
        font-weight: 400;
        font-style: Regular;
        font-size: 60px;
        leading-trim: NONE;
        line-height: 90%;
        letter-spacing: 0%;
        text-align: center;
        color: rgba(81, 60, 176, 1);
        margin: 0 0 40px;

        @include vp-1279 {
            font-size: 50px;
        }

        @include vp-767 {
            font-size: 45px;
            margin-bottom: 26px;
        }

        @media screen and (max-width: 520px) {
            font-size: 35px;
            line-height: 100%;
        }

        @include vp-389 {
            font-size: 30px;
        }
    }

    &__date {
        background: rgba(81, 60, 176, 1);
        border-radius: 30px;
        padding: 12px 30px;
        font-family: Coolvetica;
        font-weight: 400;
        font-style: Regular;
        font-size: 30px;
        leading-trim: NONE;
        line-height: 100%;
        letter-spacing: 0.5px;
        text-align: center;
        vertical-align: middle;
        color: rgba(255, 255, 255, 1);
        margin: 0 0 40px;

        @include vp-767 {
            font-size: 25px;
            margin-bottom: 30px;
        }
    }

    &__text {
        font-family: Inter;
        font-weight: 700;
        font-style: Bold;
        font-size: 18px;
        leading-trim: NONE;
        line-height: 24.8px;
        letter-spacing: 0%;
        text-align: center;
        vertical-align: middle;
        color: rgba(60, 43, 139, 1);
        margin: 0 0 80px;

        @include vp-767 {
            font-size: 16px;
            margin-bottom: 60px;
        }

        @include vp-389 {
            font-size: 14px;
        }
    }
}